






































import { Component, Vue } from 'vue-property-decorator';
import Submit from '../components/Submit.vue';
import Prizes from '../assets/prize';

@Component({
  name: 'Success',
  components: {
    Submit,
  },
})
export default class Success extends Vue {
  data: any;

  hxm = '';

  submitting = false;

  created() {
    console.log(this.$route.params.data);
    this.data = this.$route.params.data;
  }

  mounted() {
    console.log(this.data);
    if (!this.data?.id) {
      this.$router.replace({ name: 'home' });
    }
  }

  get award() {
    return this.data && this.data.award;
  }

  get phone() {
    if (this.data && this.data.phone) {
      return this.data.phone.replace(/(\d{3})\d{4}(\d{4})/, '$1****$2');
    }
    return '';
  }

  getPrize(index: number) {
    return Prizes.getPrizeImage(index);
  }

  getPrizeName(index: number) {
    return Prizes.getPrizeName(index);
  }

  onSubmit() {
    this.$router.push({ name: 'lottery' });
  }

  async receive() {
    try {
      const hxm = this.hxm.trim();
      if (hxm.length == 0) {
        this.$toast('请输入核销码');
        return;
      }
      if (this.submitting) return;
      this.submitting = true;
      const data = await this.$xhr.api.post('/lottery2023/receive', {
        params: {
          userId: this.data.id,
          hxm,
        },
      });
      if (data.awardId > 0 && data.awardStatus == 1) {
        this.$router.push({ name: 'receive', params: { data } });
        return;
      }
    } catch (error) {
      // eslint-disable-next-line
      // @ts-ignore
      this.$toast(error && error.message || '服务器开小差了！');
    } finally {
      this.submitting = false;
    }
  }
}
